const getStrLength = (str) => {
	let count = str.length;
    for (let i = 0, size = str.length; i < size; i++) {
        let cu = str.charCodeAt(i);
        if (cu >= 0xD800 && cu <= 0xDBFF && i < (size - 1)) {
            let next = str.charCodeAt(i + 1);
            if (next >= 0xDC00 && next <= 0xDFFF) {
                count--;
                i++;
            }
        }
    }
	return count;
};

export default {
	getStrLength
};







